.btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: $btn-font-weight;
  color: $body-color;
  white-space: nowrap;
  vertical-align: middle;
  background-image: none;
  border: $btn-border-width solid transparent;
  @include button-size($btn-padding, $btn-font-size, $btn-border-radius, $btn-height);
  box-shadow: $btn-box-shadow;
  cursor: pointer;
  transition: $btn-transition;

  &.disabled, &[disabled] {
    cursor: not-allowed;
    opacity: $btn-disabled-opacity;
    box-shadow: none;

    > * {
      pointer-events: none;
    }
  }
}

.btn-lg {
  @include button-size($btn-padding-lg, $btn-font-size-lg, $btn-border-radius-lg, $btn-height-lg);
}

.btn-sm {
  @include button-size($btn-padding-sm, $btn-font-size-sm, $btn-border-radius-sm, $btn-height-sm);
}

.btn-primary {
  @include button-style($primary, $primary, $white)
}

.btn-danger {
  @include button-style($danger, $danger, $white)
}

.btn-default {
  @include button-style($white, $gray-400, $body-color, $white, $primary)
}

.btn-link {
  font-weight: $font-weight-normal;
  color: $btn-link-color;
  text-decoration: $link-decoration;
  box-shadow: none;

  &:hover {
    color: $btn-link-hover-color;
    text-decoration: $link-hover-decoration;
  }

  &:focus,
  &.focus {
    text-decoration: $link-hover-decoration;
    box-shadow: none;
  }

  &:disabled,
  &.disabled {
    color: $btn-link-disabled-color;
    pointer-events: none;
  }
}