.x {
  height: 40px;
  background: #4285f4;
  margin: 4px 0;
  color: white;
}

.y {
  background: lighten(#4285f4, 5%);
  margin: 4px 0;
  color: white;
}

.z {
  background: #34a853;
  width: 100px;
  margin: 4px 0;
  color: white;
}

.z + .y, .y + .z, .z + .gu-layout {
  margin-left: 8px;
}

.outAside {
  margin-right: 8px;
}