@import "../../style/helper";

.lu-layout {
  display: flex;
  flex-direction: column;

  &-content {
    flex-grow: 1;
  }

  & & {
    flex-grow: 1;
    flex-direction: row;
  }

  &-hasAside {
    flex-direction: row !important;

    .lu-layout {
      flex-direction: column;
    }
  }
}